html,
body,
.root {
  height: 100%;
}

body {
  line-height: 1.5;
}

body,
.app {
  color: #444444;
}

h2 {
  font-size: calc(7px + 2vmin);
  margin: 0;
  overflow: hidden;
  word-break: break-word;
  text-overflow: inherit;
}

h3 {
  font-weight: 400;
  margin: 5px 0;
  font-size: calc(6px + 2vmin);
}

/* main page */

.list-books-title {
  margin: 0 0 30px 0;
  padding: 15px 0 15px 0;
  z-index: 1;
  text-transform: uppercase;
  border-bottom: 3px solid orange;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  position: fixed;
  width: 100vw;
  background: white;
}

.list-books-title h1 {
  padding: 0 0 5px 15px;
  font-weight: 700;
  margin: 0;
  letter-spacing: 2px;
  font-size: calc(18px + 2vmin);
  color: #219653;
}

.bookshelf {
  margin: 0 0 20px 10px;
  padding-bottom: 10px;
}

.bookshelf-header {
  display: flex;
  justify-content: space-between;
}

.bookshelf-title {
  font-weight: 600;
  font-size: calc(10px + 2vmin);
  color: #666666;
  padding: 0 0 0 5px;
  align-self: center;
}

.bookshelf-view {
  font-weight: 400;
  font-size: calc(7px + 2vmin);
  padding: 0 10px;
  align-self: center;
  letter-spacing: 1px;
}

.main-grid {
  width: 100%;
  max-height: 60vh;
  overflow-x: auto;
  display: flex;
  padding: 0 0 0 10px;
}

.book {
  padding-right: 5vmin;
  width: 135px;
}

.book-cover {
  box-shadow: -2px 4px 4px #444444;
}

.book-li {
  list-style: none;
}

.book-info {
  margin-top: -20px;
  line-height: 1.2;
  padding-right: 3vmin;
}

.open-search {
  z-index: 2;
  position: fixed;
  right: 15px;
  top: 10px;
}

.open-search a {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: orange;
  background-image: url("./icons/add.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  font-size: 0;
}

.open-search a:hover {
  background: #219653;
  background-image: url("./icons/add.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
}

.close-search:hover {
  opacity: 1;
}

select {
  background-color: #ffffff;
  padding: 5px 0;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
}

option:focus {
  background-color: red;
}

.list-books-title a:hover {
  opacity: 0.5;
}

.list-books-content {
  padding-top: 100px;
}
/* search page */

.search-books-bar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  display: flex;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 0 6px rgba(0, 0, 0, 0.23);
}

.search-books-input-wrapper {
  flex: 1;
}

.search-books-bar input {
  width: 100%;
  padding: 15px 10px;
  font-size: 1.25em;
  border: none;
  outline: none;
  color: orange;
}

.close-search {
  display: block;
  top: 20px;
  left: 15px;
  width: 50px;
  height: 53px;
  background: white;
  background-image: url("./icons/arrow-back.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
  font-size: 0;
  opacity: 0.7;
}

/* search results */
.search-books-results {
  margin-top: 50px;
  margin-left: auto;
}

.search-books-results {
  display: grid;
  padding: 0 10px;
  background: whitesmoke;
  grid-template-columns: 50px 1fr 50px;
}

.searchResults {
  grid-column: 1/-1;
  padding: 30px 0;
}

.results-grid {
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: repeat(auto-fill, 140px);
  column-gap: 10px;
  row-gap: 20px;
  align-content: center;
  align-items: flex-start;
  justify-content: center;
  padding: 0;
  margin-left: 25px;
}

.book-shelf-changer {
  position: relative;
  margin-left: 80px;
  bottom: 30px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #219653;
  background-image: url("./icons/arrow-drop-down.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 40px;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.16), 0 3px 3px rgba(0, 0, 0, 0.23);
}

.book-shelf-changer select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

/* Personalized */
.searchResults {
  letter-spacing: 2px;
  text-align: center;
}

.search-books-results {
  box-shadow: none!important;
}

@media only screen and (min-width: 450px) {
  .book-cover {
    background-repeat: no-repeat;
    width: 120px !important;
    height: 175px !important;
  }

  .book {
    width: 150px;
  }

  .book-shelf-changer {
    margin-left: 100px;
  }

  .book-title h2 {
    font-size: 16px;
  }

  .book-authors h3,
  .bookshelf-view {
    font-size: 14px;
  }

  .book-info {
    padding-right: 0;
  }
}

@media only screen and (min-width: 525px) {
  .list-books-title h1 {
    padding-left: 30px;
  }

  .open-search {
    right: 30px;
  }

  .list-books-content {
    padding-left: 20px;
  }
}

@media only screen and (min-width: 769px) {
  body {
    background: rgb(196, 196, 196);
  }

  .list-books {
    width: 70vw;
    margin: 5vh auto;
    box-shadow: 0px 2px 10px 1px grey;
    background: whitesmoke;
  }

  .bookshelf {
    margin: 10px 30px;
  }

  .list-books-title {
    width: 70vw;
    padding-top: 20px;
    position: absolute;
  }

  .open-search {
    right: 17vw;
    top: 7.3vh;
  }
}